import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import ConsumeType from './ConsumeType'

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    formConfig: {
      requiredNew: false,
    },
  },

  consumeTypeId: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: ConsumeType.getList,
      },
    },
  },
  totalRound: {
    formConfig: {
      default: '',
      rule: [
        v => /^[0-9]\d*$/.test(v) || 'Bitte geben Sie die richtige Zahl ein',
      ],
    },
  },
  roundTime: {
    formConfig: {
      default: '',
      rule: [
        v => /^[0-9]\d*$/.test(v) || 'Bitte geben Sie die richtige Zahl ein',
      ],
    },
  },
  maxDineTime: {
    formConfig: {
      default: '',
      rule: [
        v => /^[0-9]\d*$/.test(v) || 'Bitte geben Sie die richtige Zahl ein',
      ],
    },
  },
  roundDishCount: {
    formConfig: {
      default: '',
      rule: [
        v => /^[0-9]\d*$/.test(v) || 'Bitte geben Sie die richtige Zahl ein',
      ],
    },
  },
  childRoundDishCount: {
    formConfig: {
      default: '',
      rule: [
        v => /^[0-9]\d*$/.test(v) || 'Bitte geben Sie die richtige Zahl ein',
      ],
    },
  },
}

const config = {
  add: function (item) {
    item.roundTime = item.roundTime * 60
    item.maxDineTime = item.maxDineTime * 60
    return hillo.post('ConsumeType.php?op=addJpBuffetSetting', {
      ...item,
      langs: JSON.stringify(item.langs),
    }, {
      showLoading: true,
    })
  },
  edit: function (item) {
    item.roundTime = item.roundTime * 60
    item.maxDineTime = item.maxDineTime * 60
    return hillo.postWithUploadFile('ConsumeType.php?op=updateJpBuffetSetting', {
      ...item,
      langs: JSON.stringify(item.langs),
    }, {
      showLoading: true,
    })
  },
  remove: function (id) {
    return hillo.post('ConsumeType.php?op=deleteJpBuffetSetting', { id: id })
  },

  load: async function (filter) {
    const res = (await hillo.get('ConsumeType.php?op=showAllBuffetSetting', {
      ...filter,
    }))
      .content

    res.forEach(i => {
      i.roundTime = i.roundTime / 60
      i.maxDineTime = i.maxDineTime / 60
    })
    return res
  },
}

export default IKDataEntity.ModelFactory(entity, config)
